<template>
	<div>
		<div class="imgBox">
			<p class="title">店铺门头照</p>
			<el-upload
					class="avatar-uploader"
					action=""
					:show-file-list="false"
					:disabled="isVerified"
					accept=".jpg,.jpeg,.png,.gif,.bmp,.JPG,.JPEG,.PNG,.GIF,.BMP"
					:http-request="uploadUsersImage"
					:before-upload="beforeUpload">
				<img v-if="UsersImage" :src="UsersImage" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
		</div>
		<div class="imgBox">
			<p class="title">身份证正面</p>
			<el-upload
					class="avatar-uploader"
					action=""
					:show-file-list="false"
					:disabled="isVerified"
					accept=".jpg,.jpeg,.png,.gif,.bmp,.JPG,.JPEG,.PNG,.GIF,.BMP"
					:http-request="uploadUsersIDImgA"
					:before-upload="beforeUpload">
				<img v-if="UsersIDImgA" :src="UsersIDImgA" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
		</div>
		<div class="imgBox">
			<p class="title">身份证反面</p>
			<el-upload
					class="avatar-uploader"
					action=""
					:show-file-list="false"
					:disabled="isVerified"
					accept=".jpg,.jpeg,.png,.gif,.bmp,.JPG,.JPEG,.PNG,.GIF,.BMP"
					:http-request="uploadUsersIDImgB"
					:before-upload="beforeUpload">
				<img v-if="UsersIDImgB" :src="UsersIDImgB" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
		</div>
		<div class="imgBox">
			<p class="title">营业执照</p>
			<el-upload
					class="avatar-uploader"
					action=""
					:show-file-list="false"
					:disabled="isVerified"
					accept=".jpg,.jpeg,.png,.gif,.bmp,.JPG,.JPEG,.PNG,.GIF,.BMP"
					:http-request="uploadUsersBusinessImg"
					:before-upload="beforeUpload">
				<img v-if="UsersBusinessImg" :src="UsersBusinessImg" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
		</div>

		<div style="clear: both"></div>
		<el-button v-if="!isVerified" style="margin-top: 30px;" size="small" type="success" @click="submitUpdate">提交审核</el-button>
		<div v-if="!isVerified" style="margin-top: 20px;">
			<span>提交审核资料后请于左侧填写基本信息</span>
		</div>
	</div>
</template>

<script>
	import {mapState, mapMutations, mapActions} from 'vuex'

	export default {
		name: "MyScan",

		data() {
			return {
				isVerified: false,
				dialogImageUrl: "",
				dialogVisible: false,
				allowType: ['gif', 'jpg', 'bmp', 'jpeg', 'png'],
				UsersImage: null,
				UsersIDImgA: null,
				UsersIDImgB: null,
				UsersBusinessImg: null,
				imgBase: null,
				UsersImageOrigin: null,
				UsersIDImgAOrigin: null,
				UsersIDImgBOrigin: null,
				UsersBusinessImgOrigin: null,
			};
		},

		computed: {
			...mapState(['userInfo', 'sysConfig']),
		},

		async mounted() {
			await this.loadSysConfig();
			this.imgBase = this.sysConfig.content.ShopDomain.Value;
			if (this.userInfo.accattach.HeadImage) this.UsersImage = this.imgBase + this.userInfo.accattach.HeadImage;
			if (this.userInfo.accattach.IDImgA) this.UsersIDImgA = this.imgBase + this.userInfo.accattach.IDImgA;
			if (this.userInfo.accattach.IDImgB) this.UsersIDImgB = this.imgBase + this.userInfo.accattach.IDImgB;
			if (this.userInfo.accattach.BusinessImg) this.UsersBusinessImg = this.imgBase + this.userInfo.accattach.BusinessImg;
			this.isVerified = this.userInfo.accattach.status === 1;
		},

		methods: {
			...mapActions(['loadSysConfig']),
			...mapMutations(['SET_USER_LOGIN_INFO']),

			beforeUpload(file) {
				let typeLimit = false;
				this.allowType.forEach(item => {
					typeLimit = typeLimit || file.type === `image/${item}`;
				});
				const sizeLimit = file.size / 1024 / 1024 < 2;

				if (!typeLimit) {
					this.$Message.error('只支持gif,jpg,bmp,jpeg,png格式图片，请重试');
				} else
				if (!sizeLimit) {
					this.$Message.error('上传图片大小不能超过2MB，请重试');
				}

				return typeLimit && sizeLimit;
			},

			async uploadUsersImage(option) {
				await this.uploadImg(option, 'UsersImage');
			},

			async uploadUsersIDImgA(option) {
				await this.uploadImg(option, 'UsersIDImgA');
			},

			async uploadUsersIDImgB(option) {
				await this.uploadImg(option, 'UsersIDImgB');
			},

			async uploadUsersBusinessImg(option) {
				await this.uploadImg(option, 'UsersBusinessImg');
			},

			async uploadImg(option, target) {
				let res = await this.$request.uploadImg(option.file);
				if (res) {
					this.$Message.success('上传成功');
					this[`${target}`] = this.imgBase + res.content;
					this[`${target}Origin`] = res.content;
				} else {
					this.$Message.error('上传失败');
				}
			},

			async submitUpdate() {
				let userInfo = JSON.parse(localStorage.getItem('userInfo'));
				userInfo = userInfo.accattach;
				userInfo.Code = "";
				userInfo.Msg = "";
				userInfo.HeadImage = this.UsersImageOrigin;
				userInfo.IDImgA = this.UsersIDImgAOrigin;
				userInfo.IDImgB = this.UsersIDImgBOrigin;
				userInfo.BusinessImg = this.UsersBusinessImgOrigin;
				let res = await this.$request.updateUserInfo(userInfo);
				if (res) {
					let tmp = this.userInfo;
					tmp.accattach = userInfo;
					this.SET_USER_LOGIN_INFO(tmp);
					localStorage.setItem('userInfo', JSON.stringify(tmp));
					this.$Message.success('提交成功');
				} else {
					this.$Message.error('提交失败');
				}
			},
		}
	};
</script>

<style scoped>
	.imgBox {
		float: left;
		margin-right: 80px;
	}

	.title {
		font-size: 16px;
		font-weight: bold;
		margin: 10px 0;
	}

	.avatar-uploader {
		height: 180px;
		width: 180px;
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader:hover {
		border-color: #409EFF;
	}

	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	}
</style>



